.edit-task-modal {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(255, 255, 255, 0.8);
    display: flex;
    justify-content: center;
    align-items: flex-start;
    padding-top: 100px;
}

.edit-task-modal .modal {
    width: 80%;
    background: white;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.1);
    display: flex;
    flex-direction: column;
    border: 1px solid #eee;
    position: relative;
}

.edit-task-modal .mdi-close {
    position: absolute;
    font-size: 18px;
    right: 20px;
    cursor: pointer;
}

.edit-task-modal input {
    height: 40px;
    padding: 0 10px;
    margin: 20px 0 10px;
    font-size: 18px;
}

.edit-task-modal .actions {
    display: flex;
    justify-content: space-between;
}

.edit-task-modal .actions a {
    background-color: #F06A6A;
    color: white;
    display: block;
    padding: 10px;
    border-radius: 5px;
    margin-top: 20px;
    font-weight: bold;
    text-decoration: none;
}
