.tab-bar {
    background-color: #004BA8;
    height: 70px;
    color: white;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}

.tab-bar .tab {
    text-align: center;
    font-size: 10px;
}
.tab-bar .tab .mdi {
    display: block;
    font-size: 32px;
    line-height: 32px;
}
