.add-task {
    border-bottom: 1px solid #ccc;
    display: flex;
    align-items: center;
    margin: 5px 0;
}

.add-task input {
    height: 40px;
    display: block;
    width: 100%;
    font-size: 18px;
    padding: 0 10px;
    border: 0;
    background: none;
}

.add-task a {
    background-color: #F06A6A;
    display: flex;
    width: 26px;
    height: 26px;
    border-radius: 50%;
    justify-content: center;
    align-items: center;
    color: white;
}
